/* Reset default */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  width: 100vw;
  height: auto;
  position: relative;
  background-color: #373a3b;
}
.top {
  width: 100%;
  height: 20px;
  color: #fff;
  font-size: 12px;
  line-height: 20px;
  position: relative;
}
.top_mota {
  width: 600px;
  margin: auto;
}
.menu {
  background-color: #fff;
  /* margin: 0 64px 0 64px; */
}

.m_container {
  height: 60px;
  width: 600px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu-logo {
  width: 10%;
  height: 100%;
  display: flex;
  justify-items: center;
  align-items: center;
}
.menu-items {
  width: 90%;
  list-style: none;
}
.menu-items > li {
  font-size: 14px;
  display: inline;
  padding: 2px 8px 2px 8px;
}

.overview {
  background-color: rgb(146, 142, 137);
  width: 100%;
}
.ov-container {
  margin: auto;
  width: 600px;
  height: 800px;
  /* height: 100%; */
}
.gioi_thieu {
  height: 100%;
  background-color: rgb(83, 85, 85);
}

.lich_su_hanh_trinh {
  height: 600px;
  width: 100%;
  /* background-color: rgb(69, 195, 107); */
}
.thanh_tuu {
  margin-top: 30px;
  height: 30%;
  color: #fff;
  /* background-color: rgb(104, 204, 204); */
}
.gt-top {
  width: 100%;
  height: 40%;
  display: flex;
  /* background-color: rgb(214, 183, 59); */
}
.gt-bottom {
  width: 100%;
  height: 60%;
  display: flex;
  color: #fff;
  /* background-color: rgb(224, 148, 199); */
  justify-content: space-around;
}
.gt-left {
  /* background-color: cornsilk; */
  width: 40%;
}
.gt-right {
  /* background-color: rgb(179, 161, 91); */
  width: 60%;
}

.gt-left > h2 {
  color: #fff;
  padding: 20px 20px 10px 10px;
}
.gt-left > p {
  font-size: 10px;
  color: #fff;
  padding: 0px 20px 10px 10px;
}

.gt-right > h4 {
  color: #fff;
  padding: 20px;
}
.gtr_list {
  padding: 20px;
  font-size: 10px;
  height: 75%;
}

.gtr_item {
  width: 100%;
  height: 50%;
  display: flex;
}
.gtr_item > div > p {
  color: #b4b2aa;
}
.gtr_item > div > .detail {
  padding: 10px;
  color: #fff;
}

.gt_active {
  color: #000;
}
.tam_nhin {
  width: 30%;
  height: 80%;
  background-color: rgb(235, 155, 42);
  clip-path: polygon(0% 50%, 0% 100%, 100% 100%, 100% 0%);
}
.su_menh {
  width: 30%;
  height: 80%;
  background-color: rgba(140, 135, 137, 1);
  clip-path: polygon(0% 50%, 0% 100%, 100% 100%, 100% 0%);
}
.cot_loi {
  width: 30%;
  height: 80%;
  background-color: rgb(140, 135, 137);
  clip-path: polygon(0% 50%, 0% 100%, 100% 100%, 100% 0%);
}
.tn_container {
  height: 50%;
  top: 60%;
  padding: 0 10px;
  position: relative;
}
.tn_container > h4 {
  margin-bottom: 10px;
}
.tn_container > p {
  font-size: 10px;
}
.ls_mota {
  height: 30%;
  background-color: rgb(119, 130, 130);
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.ls_mota > p {
  font-size: 10px;
  width: 60%;
}

.tt_content {
  padding: 10px 0;
  width: 100%;
  /* background-color: antiquewhite; */
  display: flex; /* Xếp các phần tử ngang hàng */
  align-items: center; /* Căn chỉnh các phần tử theo trục dọc */
  justify-content: space-between;
  border-bottom: #fff solid 1px;
}

.ttc_mo_ta {
  width: 56%;
}

.tt_content img {
  max-width: 200px; /* Đặt kích thước tối đa cho hình ảnh */
  height: auto; /* Để đảm bảo hình ảnh giữ nguyên tỷ lệ */
}

.tt_content > p {
  width: 60px;
  text-align: right;
  margin: 5px 0; /* Khoảng cách giữa các đoạn văn và hình ảnh */
}

.doi_ngu_nhan_su {
  width: 100%;
  height: 200px;
  background-color: red;
}
